.primary-link {
    display: block;
    margin: 2rem;
    background-color: #17924f;
    color: #fff;
    padding: 0.4rem 1.3rem;
    border: none;
    cursor: pointer;
    font-size: 1rem;
    outline: none;
    transition: all 0.2s ease-in;
    box-sizing: border-box;
    text-decoration: none;
    text-align: center;
    text-transform: capitalize;
    font-weight: 300;
}


.primaryBtn {
    border: none;
    border-radius: 0.3rem;
    outline: none;
    font-size: 1.2rem;
    padding: 0.5rem 1rem;
    cursor: pointer;
    font-weight: 300;
}


.iconButton {
    border: none;
    border-radius: 0.3rem;
    outline: none;
    font-size: 1.2rem;
    padding: 0.5rem .7rem;
    cursor: pointer;
}


.BackButton {
    margin-bottom: 1rem;
    background-color: #f1f1f182;
    padding: 0.5rem 1rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 1.2rem;
    width: fit-content;
    text-decoration: none;
    color: unset;

    .BackButton-icon {
        width: 2rem;
        height: 2rem;
    }
}
